.bodyBlock {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 50vh;
    opacity: 0.8;
}

ul {
    position: relative;
    transform: skewY(-20deg);

}
ul li {
    position: relative;
    list-style: none;
    width: 150px;
    background: #3e3f46;
    padding: 15px;
    transition: 0.5s;
    z-index: calc(1 * var(--i));
    font-weight: 700;
    font-size: 1rem;
}

ul li:hover {
    transform: translateX(-50px);
    background: var(--clr);
}

ul li::before {
    content: '';
    position: absolute;
    top: 0;
    left: -40px;
    width: 40px;
    height: 100%;
    background: #3e3f46;
    filter: brightness(0.7);
    transform-origin: right;
    transform: skewY(45deg);
    transition: 0.5s;
 }

ul li:hover::before {
    /*background: var(--clr);*/
    filter: brightness(0.7);
}

ul li::after {
    content: '';
    position: absolute;
    top: -40px;
    left: 0;
    width: 100%;
    height: 40px;
    background: #3e3f46;
    filter: brightness(0.9);
    transform-origin: bottom;
    transform: skewX(45deg);
    transition: 0.5s;
}

ul li:hover::after {
    background: var(--clr);
    filter: brightness(0.9);
}

ul li a {
    text-decoration: none;
    color: #bdbdbd;
    /*display: block;*/
    /*text-transform: uppercase;*/
    letter-spacing: 0.05em;
    transition: 0.5s;
}

ul li:hover a {
    color: #fff;
}

ul li:last-child:after {
    box-shadow: -100px 100px 15px rgba(0,0,0,0.35);
}

ul li span {
    position: absolute;
    top: 0;
    left: -40px;
    width: 40px;
    height: 100%;
    transform-origin: right;
    transform: skewY(45deg);
    transition: 0.5s;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0.5;
    font-size: 1.5rem;
}

ul li:hover span {
    opacity: 1;
    color: var(--clr);
}